  }
}

.featured {
    font-family: 'Roboto', sans-serif;
  width: 100%;
  display: flex;
  justify-content: space-between;
  
}

.fl{
    text-decoration: none;
}

.featuredItem {
  flex: 1 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  width: 60%;
  cursor: pointer;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  -webkit-backdrop-filter: blur(35px);
          backdrop-filter: blur(35px);
  background-color: rgba(255, 255, 255, 0.8);
}

.featuredTitle{
    font-size: 20px;
}

.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}

.featuredIcon.negative{
    color: red;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}

@media screen and (max-width: 900px){
    .featured-m{
        display: flex;
        flex-direction: column;
        width: 85%;
    }

    .fl{
        width: 86%;
