        width: 40px;
        height: 40px;
        overflow: hidden;
        border-radius: 50%;
        display: inline-block;
    }
    
    .side-menu.inactive .side-menu-footer .avatar img{
        display: none;
        max-width: 100%;
    }
    
    .side-menu-footer .user-info{
        display: inline-block;
        margin: 0 10px;
        color: #fff;
        position: absolute;
        opacity: 1;
        transition: opacity .2s ease-in;
    }
    
    .side-menu.inactive .side-menu-footer .user-info{
        opacity: 1;
        width: 0;
        height: 0;
        overflow: hidden;
    }
    
    
    .side-menu-footer .user-info h5{
        font-size: 15px;
    }
    
    
    .side-menu-footer .user-info p{
        font-size: 14px;
    }
    
    .divider{
        width: 100%;
        height: 1px;
        align-items: center;
        border-radius: 1px;
        background: #eee;
        
    }
    
    .container{
        margin-left: 0px;
        transition: margin-left .2s ease-in;
        padding: 00px;
        box-sizing: border-box;
    }
    
    .container.inactive{
        margin-left: 0px;
    }
    
    .icons{
      display: block;
      height: 100%;
      color: #eee;
    }
    
    .link{
        text-decoration: none;
        color: #fff;
    }
    
    .main-menu button{
        justify-content: space-evenly;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 350px;
        min-width: 250px;
        height: 40px;
        border: none;
        margin: .5rem 0;
        box-shadow: 0px 14px 9px -15px rgba(0, 0, 0, 0.25);
        border-radius: 8px;
        background-color: #70edb9;
        color: #fff;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s ease-in;
    }
}


